.ao-header {
	display: flex;
	flex-direction: column;
	background-color: white;
}

.ao-header__content {
	display: flex;
	justify-content: space-between;
	flex-direction: column-reverse;
	align-items: center;
	padding: 1.5rem 2.5rem;
}

@media (min-width: 1024px) {
	.ao-header__content {
		/* padding: 1.125rem 7.75rem; */

		margin: 0 auto;
		width: 100%;
		max-width: 1260px;
		/* padding: 1.125rem 1rem; */
		flex-direction: row;
	}
}

.ao-header__logo {
	width: auto;
	max-width: 230px;
	object-fit: contain;
	height: 60px;
}
.ao-header__logo img {
	width: 100%;
	object-fit: contain;
	height: 100%;
	max-width: 5.25rem;
}

@media (min-width: 1024px) {
	.ao-header__logo img {
		max-width: 6.25rem;
	}
}

.ao-header__phone-number {
	display: flex;
	align-items: center;
	padding: 0.5rem;
	background-color: #f1f1e6;
	border-radius: 10px;
	gap: 0.625rem;
	font-size: 0.9rem;
	margin-bottom: 10px;
}

@media (min-width: 1024px) {
	.ao-header__phone-number {
		font-size: 1.125rem;
		margin-bottom: 0;
	}
}

.ao-header__phone-icon {
	/* padding: 0.375rem; */
	border-radius: 7px;
	background-color: var(--primaryBlue2);
	display: flex;
	align-items: center;
	justify-content: center;
	flex: 0 0 32px;
	width: 32px;
	height: 32px;
}

.ao-header__phone-icon img {
	width: 60%;
	height: 60%;
	object-fit: contain;
}

@media (min-width: 1024px) {
	.ao-header__phone-icon {
		/* padding: 0.625rem; */
		flex: 0 0 40px;
		width: 40px;
		height: 40px;
	}

	/* .ao-header__phone-icon img {
		max-width: none;
	} */
}

.ao-header__phone-number a {
	text-decoration: underline;
	font-weight: bold;
	color: var(--primaryBlue2);
	margin-left: 0.2rem;
}
